html {
  scroll-behavior: smooth;
}

* {
  margin: 0;
  box-sizing: border-box;
  padding: 0;
  text-decoration: none;
  borer: none;
  outline: none;
}

:root {
  --primary-gray: #acb0b9;
  --primary-P08: #747b8b;
  --primary-P07: #8d8f9b;
  --primary-P04: #babdc5;
  --primary-P03: #c7cad1;
  --primary-P02: #e3e5e8;
  --primary-P06: #8b919e;
  --primary-P01: #f1f2f3;
  --primary-black: #050810;
  --primary-black2: #090f1d;
  --disabled-border: #e3e3e3;
  --disabled-border2: #e6e6e6;
  --primary-font: "Plus Jakarta Sans";
  --primary-helvetica: "Helvetica Neue', sans-serif";
  --border-xs: 6px;
  --border-sm: 12px;
}

.container {
  width: fixed;
}

nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  top: 0;
  padding: 8px 100px;
  background-color: transparent; /* White with full opacity */
  font-size: 14px;
  margin-bottom: 12px;
  position: fixed;
  box-sizing: border-box;
  width: 100%;
  z-index: 99;
  transition: background-color 0.8s ease-out, backdrop-filter 0.5s ease-out,
    padding 0.5s ease-out;
  backdrop-filter: blur(0);
}

.dropdown {
  position: relative;
  display: inline-block;
  color: pointer;
  display: block;
  gap: var(--border-xs);
}

#option {
  color: #000;
}

.dropdown:hover .dropdown-menu {
  list-style: none;
  text-decoration: none;
}

.dropdown-menu {
  display: none;
  position: absolute;
  top: 100%;
  left: 0;
  background-color: #fff;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
  border-radius: 4px;
  text-decoration: none;
  padding: 0;
  min-width: 100px;
}

.dropdown-menu li {
  padding: 8px 10px;
  z-index: 9;
  text-decoration: none;
}

.dropdown-menu a:hover {
  background-color: #f1f1f1;
}

.dropdown:hover .fa-solid {
}

.dropdown-menu li:hover {
  background-color: #f1f1f1;
  text-decoration: none;
  text-transform: none;
}

.nav-links .li {
  font-family: var(--primary-font);
  display: flex;
  gap: 24px;
  list-style: none;
  align-items: center;
  cursor: pointer;
  color: var(--primary-gray);
}

.nav-links .li a {
  color: var(--primary-gray);
}

.nav-links button {
  display: none;
}

/* .nav-links a {
  display: none;
} */

.login-togle a {
  display: none;
}

.nav-links .dropdown-menu a {
  text-decoration: none;
  cursor: pointer;
  color: var(--primary-gray);
}

.dropdown-menu a {
  text-decoration: none;
}

.dropdown-menu a:hover {
  text-decoration: none; /* Jika ingin tetap tanpa underline saat hover */
}

.nav-links li {
  position: relative;
  display: inline-block;
}

.nav-links li::after {
  content: "";
  position: absolute;
  z-index: -20;
  right: 25%;
  top: 28px;
  width: 0;
  height: 2px; /* Adjust the height of the underline */
  background-color: black; /* Color of the underline */
  transition: width 0.4s ease, left 0.4s ease;
}

.nav-links li:hover::after {
  width: 50%; /* Set the width of the underline to half of the text */
  left: 0; /* Center the underline */
}

.auth-buttons {
  display: flex;
  gap: 12px;
  align-items: center;
