.start-now {
  margin-top: 20px;
}

.start-now button {
  background-color: var(--primary-black);
  color: white;
  padding: 16px 20px;
  border-radius: 16px;
  border: none;
  cursor: pointer;
  margin-bottom: 10px;
  position: relative;
  z-index: 1;
  overflow: hidden;
  transition: color 0.3s ease-in-out;
}

.start-now button::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(to right, #6340ff, #ff40c6, #ff8040);
  border-radius: 16px;
  z-index: -1;
  transition: all 0.4s ease-in-out;
}

.start-now button::after {
  content: "";
  position: absolute;
  top: 2px;
  left: 2px;
  right: 2px;
  bottom: 2px;
  background-color: var(--primary-black);
  border-radius: 14px;
  z-index: 0;
  transition: all 0.4s ease-in-out;
}

.start-now button:hover::before {
  width: 100%;
}

.start-now button:hover::after {
  background-color: #fff; /* Warna saat hover */
}

.start-now button span {
  position: relative;
  color: #fff;
  text-align: center;
  z-index: 2;
  transition: color 0.3s ease-in-out;
}

.start-now button span a {
  color: #fff;
  text-decoration: none;
}

.start-now button:hover span a {
  color: #000; /* Warna teks saat hover */
  animation: scaleUp 0.3s ease-in-out;
}

@keyframes scaleUp {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(0.95);
  }
  100% {
    transform: scale(1);
  }
}

